import React from 'react';
import './Plus.css';
import  LikeIcon from './benefits_like_green.svg'; //    SVG
import  DislikeIcon from './benefits_like_red.svg'; //    SVG

const Plus = () => {
    return (
        <div className="container-grid">
            <div className="title-plus">
                <div className="title-custom">    ?</div>
            </div>
            <div className="row-plus">
                <div className='ocenka like'>
                    <img src={LikeIcon} alt='like' className='grade' />
                    <div className="left-grid">
                        <div className="grid-item item-g_1">    ,  24   ,      .</div>
                        <div className="grid-item item-g_2">  ,           .</div>
                        <div className="grid-item item-g_3">      ,    .</div>
                        <div className="grid-item item-g_4">     (),   .</div>
                        <div className="grid-item item-g_5">  ,    ,         .</div>
                    </div>
                </div>
                <div className="ocenka dislike">
                    <img src={DislikeIcon} alt='dislike' className='grade' />
                    <div className="right-grid">
                        <div className="grid-item item-left1">,         .</div>
                        <div className="grid-item item-left2">    ,   .</div>
                        <div className="grid-item item-left3">    .</div>
                        <div className="grid-item item-left4">      .</div>
                        <div className="grid-item item-left5">    ,           .</div>
                    </div>
                </div>
            </div>
        </div>
    );
};


export default Plus;
