import React from 'react';
import Slider from 'react-slick';
import './Reviews.css';
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";

const reviewsData = [
  {
    name: " ",
    date: "12.03.2023",
    stars: 5,
    review: "  !   .",
    image: "https://via.placeholder.com/100" //      
  },
  {
    name: " ",
    date: "15.04.2023",
    stars: 5,
    review: "     . !",
    image: "https://via.placeholder.com/100"
  },
  {
    name: " ",
    date: "20.05.2023",
    stars: 5,
    review: "      .  !",
    image: "https://via.placeholder.com/100"
  },
  {
    name: " ",
    date: "25.06.2023",
    stars: 5,
    review: " ,    .    !",
    image: "https://via.placeholder.com/100"
  },
  {
    name: " ",
    date: "12.03.2023",
    stars: 5,
    review: "  !   .",
    image: "https://via.placeholder.com/100" //      
  },
  {
    name: " ",
    date: "15.04.2023",
    stars: 5,
    review: "     . !",
    image: "https://via.placeholder.com/100"
  },
  {
    name: " ",
    date: "20.05.2023",
    stars: 5,
    review: "      .  !",
    image: "https://via.placeholder.com/100"
  },
  {
    name: " ",
    date: "25.06.2023",
    stars: 5,
    review: " ,    .    !",
    image: "https://via.placeholder.com/100"
  },
];

const settings = {
  

  dots: false,
  infinite: true,
  speed: 500,
  slidesToShow: 4,
  slidesToScroll: 1,
  responsive: [
    {
      breakpoint: 768, //  
      settings: {
        slidesToShow: 2,
        slidesToScroll: 1,
        dots: false,
        arrows: true
      }
    },
    {
      breakpoint: 480, //   
      settings: {
        slidesToShow: 1,
        slidesToScroll: 1,
        dots: false,
        arrows: true //     
      }
    }
  ]
};

const Reviews = () => {
  return ( <div className="hello">
    
    <div className="reviews-container">
      <div className="reviews-title"> </div>
      <Slider {...settings}>
        {reviewsData.map((review, index) => (
          <div className="review-card" key={index}>
            <img className="review-image" src={review.image} alt={review.name} />
            <div className="review-content">
              <div className="review-header">
                <span className="review-name">{review.name}</span>
                <span className="review-date">{review.date}</span>
              </div>
              <div className="review-stars">
                {[...Array(review.stars)].map((_, i) => (
                  <svg key={i} width="20" height="20" fill="#D7AC8A">
                    <polygon points="10,1 12.09,7.36 19.18,7.36 13.54,11.64 15.64,18 10,13.88 4.36,18 6.46,11.64 0.82,7.36 7.91,7.36" />
                  </svg>
                ))}
              </div>
              <p className="review-text">{review.review}</p>
              <div className="review-footer">
                <span className="review-logo"></span>
                <span className="review-source">{index % 2 === 0 ? "2" : "."}</span>
              </div>
            </div>
          </div>
        ))}
      </Slider>
      <div className="more-reviews">
        {/* <h2 className="more-reviews-title"> <br />  :</h2> */}
        <div className="buttons-container">
          <button className="button-2gis">   2</button>
          <button className="button-yandex">   .</button>
        </div>
      </div>
    </div>
    </div>
  );
};

export default Reviews;
