import React, { useEffect, useState } from 'react';

import './Achive.css';

const Achive = () => {
    const [value1, setValue1] = useState(0);
    const [value2, setValue2] = useState(0);
    const [value3, setValue3] = useState(0);


    const animateCounter = (setValue, targetValue, duration) => {
        const start = performance.now(); //   
        const animate = (currentTime) => {
            const elapsed = currentTime - start; //   
            const progress = Math.min(elapsed / duration, 1); //    0  1
            setValue(Math.floor(progress * targetValue)); //   

            if (progress < 1) {
                requestAnimationFrame(animate); //  ,     
            }
        };
        requestAnimationFrame(animate); //  
    };

    //  useEffect,   ,   
    useEffect(() => {
        const duration = 2000; //    
        
        animateCounter(setValue1, 27, duration); //    
        animateCounter(setValue2, 10173, duration); //    
        animateCounter(setValue3, 10000, duration); //    
    }, []);


  return ( <div className="privet">
    <div className="counter-block">
      <div className="counter-container">
        <h3 className="word-achive "></h3>
        <span className="counter ">{value1}</span>
        <p className="word-achive "> </p>
      </div>
      <div className="counter-container">
        <h3 className="word-achive "> </h3>
        <span className="counter ">{value2}</span>
        <p className="word-achive "></p>
      </div>
      <div className="counter-container">
        <h3 className="word-achive "> </h3>
        <span className="counter ">{value3}</span>
        <p className="word-achive "> </p>
      </div>
    </div>

    </div>
  );
};

export default Achive;
